<template>
  <section class="explore__items" v-if="!isLoading">
    <div
      :class='"explore__item explore__item__"+val.path'
      v-for="(val, key) in tiers"
      :key="key"
      @click="selectTier(val.path)"
    >
      <p class="explore__item__dom">{{val.alias}}</p>
      <p class="explore__item__sub">{{val.tier}}</p>
    </div>
  </section>
  <section v-else class="explore__loader">
    <div class="lds-ellipsis"><div></div><div></div><div></div><div></div></div>
  </section>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
export default {
  title: 'Home',
  data() {
    return {
      tiers: [
        { tier: "Over Used", alias: "OU", path: "ou" },
        { tier: "Under Used", alias: "UU", path: "uu" },
        { tier: "Battle Stadium Singles", alias: "BSS", path: "bss" },
        { tier: "Ubers", alias: "UBERS", path: "ubers" },
        { tier: "Video Game Championship", alias: "VGC", path: "vgc" },
        { tier: "Rarely Used", alias: "RU", path: "ru" },
        { tier: "Monotype", alias: "Mono", path: "monotype" }
      ],
    };
  },
  methods:{
    selectTier(tier){
      this.$store.commit("dex/SET_LOADING_STATE", true)
      axios.get(`/pokemon/${tier}`)
        .then(res=>{
          this.$store.commit("dex/SET_BATTLEDEX", res.data.pokemon)
          this.$router.push({path:`/dex/${tier}`})
        })
    }
  },
};
</script>
